<template>
  <div class="formulario-container">
    <h2>Formulario</h2>
    <div class="buttons-container">
      <button @click="currentAction = 'elegir'">Elegir Formulario</button>
      <button @click="currentAction = 'agregar'">Agregar Formulario</button>
      <button @click="currentAction = 'eliminar'">Eliminar Formulario</button>
    </div>

    <div v-if="currentAction === 'agregar'" class="agregar-formulario">
      <h3>Agregar Formulario</h3>
      <input type="file" @change="handleFileUpload" />
      <button @click="uploadImage" :disabled="!file">Subir Imagen</button>
      <div v-if="uploadedImageUrl" class="image-preview">
        <h4>Imagen cargada:</h4>
        <img :src="uploadedImageUrl" alt="Formulario" class="uploaded-image" />
      </div>
    </div>

    <div v-if="currentAction === 'elegir'">
      <p>Esta secci칩n mostrar치 los formularios disponibles para elegir.</p>
    </div>
    <div v-if="currentAction === 'eliminar'">
      <p>Esta secci칩n permitir치 eliminar formularios existentes.</p>
    </div>
  </div>
</template>

<script>
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

export default {
  name: "FormularioView",
  data() {
    return {
      currentAction: null,
      file: null,
      uploadedImageUrl: null,
    };
  },
  methods: {
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        this.file = file;
        this.uploadedImageUrl = URL.createObjectURL(file); // Vista previa local
      }
    },
    async uploadImage() {
      if (!this.file) return;

      const storage = getStorage();
      const storageRef = ref(storage, `formularios/${this.file.name}`);
      try {
        // Subir la imagen a Firebase Storage
        await uploadBytes(storageRef, this.file);
        const downloadURL = await getDownloadURL(storageRef);
        this.uploadedImageUrl = downloadURL;
        alert("Imagen subida correctamente.");
      } catch (error) {
        console.error("Error al subir la imagen:", error);
      }
    },
  },
};
</script>

<style scoped>
.formulario-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f9f9f9;
}

.buttons-container {
  display: flex;
  gap: 20px;
  margin: 20px 0;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

.agregar-formulario {
  margin-top: 20px;
  text-align: center;
}

.image-preview {
  margin-top: 20px;
}

.uploaded-image {
  max-width: 300px;
  max-height: 300px;
  margin-top: 10px;
  border: 2px solid #ccc;
  border-radius: 10px;
}
</style>
